  height: 36px;
  margin-bottom: 18px;
}
.v-select .dropdown-toggle {
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
  border-bottom-color: #9e9e9e !important;
  border-radius: 0 !important;
}
.dropdown-menu li{
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  padding-left: 5px;
}
.dropdown-menu li a {
  width: 100%;
}
select {
  width: 100%;
  overflow: hidden;
}



