




















@component-namespace mint {
  @component loadmore {
    overflow: hidden;

    @descendent content {
      @when dropped {
        -webkit-transition: .2s;
        transition: .2s;
      }
    }

    @descendent top, bottom {
      text-align: center;
      height: 50px;
      line-height: 50px;
    }

    @descendent top {
      margin-top: -50px;
    }

    @descendent bottom {
      margin-bottom: -50px;
    }

    @descendent spinner {
      display: inline-block;
      margin-right: 5px;
      vertical-align: middle;
    }

    @descendent text {
      vertical-align: middle;
    }
  }
}



