  max-height: 80%
}
.modal .modal-content {
  padding: 25px 0 !important;
}
.card-image {
  padding-top: 0 !important;
}
.itemList {
  color: #666
}
.itemList li {
  background: white;
  border: 1px solid#eee;
  height: 50px;
  line-height: 50px;
  /*padding-left: 20px;*/
  margin: 0 10px;
  color: #666;
}
.itemList li a {
  color: #666;
  display: block;
  width: 100%;
  height: 50px;
  font-size: 15px;
}
.menu-content{
  position: fixed;
  top: 0;
  width: 100%;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  line-height: 50px;
  padding: 0 20px;
  color: white;
  background: #26a69a;
  font-size: 21px;
  z-index: 99999999;
  box-shadow: 0 1px 2px #999;
}
#dropdown2 {
  width: 50% !important;
  padding: 0;
  z-index: 9999;
  right: 0 !important;
  left: auto !important;
}
#dropdown2 a {
  color: #666;
}
.v-select>.dropdown-menu {
  padding: 3px;
  border: 1px solid#9e9e9e;
}
.menu {
  color: white;
}
